@import '../../style/mixin';
@import '../../style/variables';

.collapse-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  max-width: 582px;
  // height: 52px;
  padding: 10px 20px 10px 10px;
  background-color: #FF6060;
  border-radius: 8px;
  color: white;
  font-family: 'Montserrat';
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 0px;
  @include responsive($breakpoint) {
    // max-width: 335px;
    // max-height: 30px;
    // margin-bottom: 20px;
  }
}

.collapse-header .arrow {
  font-size: 18px;
  transition: transform 4.75s ease;
}

.collapse-header .arrow.open {
  transform: rotate(180deg);
}

.collapse-content {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  color: black;
  padding: 10px;
  background-color: #F6F6F673;
  border-radius: 8px;
  margin-top: 0px;
  list-style-type: none;
}


  .about-Values {
    display: flex;
    flex-direction: column;
    align-items: center;

    .collapse-container {
      width: 70%;
      @include responsive($breakpoint) {
        width: 89%;
      }

      .collapse-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        padding: 15px 20px;
        background-color: #FF6060; /* Distinctive header color */
        border-radius: 8px;
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-size: 18px;
        font-weight: 700;
        transition: background-color 0.3s ease; /* Hover effect */
        margin: 30px 0 0 0;
        max-width: initial;
        @include responsive($breakpoint) {
          padding: 7px 20px;
        }

    
        &:hover {
          background-color: #ff4d4d; /* Slightly darker on hover */
        }
    
        .arrow {
          display: flex;
          align-items: center;
          transition: transform 1s ease; /* Smooth arrow rotation */
    
          .arrow-icon {
            width: 24px; /* Adjust size of the arrow icon */
            height: 13px;
          }
    
          &.open {
            transform: rotate(180deg); /* Rotate arrow when open */
          }
        }
      }
    
      .collapse-content {
        font-family: 'Montserrat', sans-serif;
        font-size: 18px;
        color: black;
        background-color: #F6F6F673;
        border-radius: 8px;
        line-height: 1;
        overflow: hidden;
        transition: max-height 1s ease, opacity 1s ease, visibility 1s ease;

        margin-block-start: 0;
        margin-block-end: 0;
        margin-inline-start: 0px;
        margin-inline-end: 0px;

        &.open {
          max-height: 1000px; /* Large enough to fit the content */
          opacity: 1;

        }
      }
    }
  }    